export default {
	props: {
		// 
		dashed: {
			type: Boolean,
			default: false
		},
		// 
		hairline: {
			type: Boolean,
			default: true
		},
		// text
		dot: {
			type: Boolean,
			default: false
		},
		// left-center-right-
		textPosition: {
			type: String,
			default: 'center'
		},
		// 
		text: {
			type: [String, Number],
			default: ''
		},
		// 
		textSize: {
			type: [String, Number],
			default: 14
		},
		// 
		textColor: {
			type: String,
			default: '#909399'
		},
		// 
		lineColor: {
			type: String,
			default: '#dcdfe6'
		},
		...uni.$uv?.props?.divider
	}
}